#none-club {
	width: 100%;
	text-align: center;
	> p {
		color: #999;
		font-size: 1.5rem;
	}
	> a {
		display: inline-block;
		margin: 1.5rem 0;
		padding: .7rem 3rem;
		font-size: 1.6rem;
		border-radius: 20px;
		color: #fff;
		background-color: $mainColor;
	}
}

// club-select
#cselect-top {
	padding: .1rem .3rem 0 .8rem;
	line-height: 4.5rem;
	color: #111;
	background-color: #eee;
	img {
		width: 2.6rem;
		margin-right: .2rem;
		vertical-align: -.8rem;
	}
}
#club-list {
	padding: .2rem .6rem;
}
.club-item {
	padding: .7rem 0;
	border-bottom: 1px solid #eee;
	> .portrait-wrap {
		float: left;
		width: 26%;
		line-height: 7rem;
		text-align: center;
		padding-left: 1rem;	
	}
	.portrait {
		width: 90%;
		height: auto;
	}
}
.club-info {
	width: 74%;
	padding-left: .5rem;
	> h2 {
		color: #000;
		font-size: 1.7rem;
	}
	> h3 {
		color: #888;
		margin: .5rem 0;
	}
	p {
		color: #222;
	}
}

/*club-detail*/
#cdetail-top {
	position: relative;
	padding: 1.3rem 0;
	text-align: center;
	color: #fff;
	background-color: $mainColor;
	> h2 {
		margin: .8rem 0;
	}
	> img { 
		width: 7.2rem;
		height: 7.2rem;
	}
	> button {
		position: absolute;
		right: .8rem;
		top: 1.2rem;
		height: 2.7rem;
		padding: 0 1.5rem;
		font-size: 1.1rem;
		color: #fff;
		border: none;
		outline: none;
		border-radius: 2rem;
		background-color: #4d8074;
	}
}
.info-title {
	padding: .7rem .12rem;
	color: #000;
	font-size: 1.7rem;
	border-bottom: 1px solid #ddd;
}
.info-wrap {
	padding: .5rem 1.2rem;
	background-color: #fff;
}
#club-member {
	margin-top: .7rem;
	color: #111;
	li {
		float: left;
		width: 20%;
		text-align: center;
		font-size: 1.3rem;
		img {
			width: 5.6rem;
			height: 5.6rem;
			border-radius: 100%;
		}
	}
}

/*club-create*/
#ccreate-wrap {
	color: #000;
	font-size: 1.6rem;
}
.ccreate-title {
	padding: 1.1rem .7rem;
}
.ccreate-detail {
	margin-bottom: 1rem;
	li {
		padding: .7rem;
		background-color: #fff;
		> span {
			display: inline-block;
			width: 8rem;
			margin-right: .5rem;
		}
		> input[type='text'] {
			width: 18.5rem;
			height: 2.8rem;
			border: none;
			outline: none;
			font-size: 1.6rem;
			vertical-align: 2px;
		}
		> textarea {
			width: 18.5rem;
			height: 8rem;
			resize: none;
			outline: none;
			padding: .3rem;
			font-size: 1.6rem;
			border: none;
			vertical-align: text-top;
		}
		> img {
			position: absolute;
			right: 1rem;
			top: 1.2rem;
			width: 1rem;
		}
	}
}
#club-logo-set {
	position: static;
	width: 3.5rem;
	height: 3.5rem;
	border-radius: 100%;
	+ img {
		top: 1.6rem;
	}
}
.club-add-img {
	padding: .7rem;
	background-color: #fff;
	> img {
		width: 6rem;
		height: 6rem;
		border-radius: 5px;
		margin-right: 1.2rem;
	}
	> span {
		color: #aaa;
		font-size: 1.4rem;
	}
}

.date-picker {
	position: fixed;
	left: 0;
	bottom: -17.5rem;
	width: 100%;
	text-align: center;
	border-top: 1px solid #eee;
	background-color: #fff;
	transition: bottom 200ms;
	> p {
		color: #111;
		font-size: 1.9rem;
		padding: 1rem;
		text-align: center;
		> span {
			margin-top: -.3rem;
			font-size: 1.8rem;
		}
	}
}
.dpicker-body {
	height: 9.8rem;
	overflow: scroll;
}
.dpicker-item {
	width: 100%;
	height: 3.8rem;
	line-height: 3.8rem;
	font-size: 2.5rem;
	color: #999;
	transform: scaleY(0.6);
	&.sub-active {
		color: #666;
		transform: scaleY(0.8);
	}
	&.active {
		color: #000;
		transform: scaleY(1);
	}
}

/*club-homepage*/
#homepage-top {
	height: 17rem;
	position: relative;
}
#homepage-intro {
	float: left;
	width: 35%;
	height: 17rem;
	text-align: center;
	background-color: $mainColor;
	text-align: center;
	color: #fff;
	font-size: 1.5rem;
	> img {
		width: 70%;
		max-width: 15rem;
		max-height: 15rem;
		border-radius: 100%;
		margin: 1.5rem 0 1rem;
	}
}
#homepage-banner {
	.imgbox img {
		height: 17rem;
	}
	.imgbox li {
		height: 17rem;
	}
	> .page ol {
		left: auto;
		right: .5rem;
		li {
			left: 0;
		}
	}
}
#homepage-actlist {
	padding: 1rem 0 .6rem;
	margin-bottom: 4.8rem;
}
.homepage-submit {
	position: fixed;
	bottom: 0;
	left: 0;
	width: 100%;
	> .submit {
		width: 100%;
		margin: 0;
	}
}
.homepage-watch {
	position: absolute;
	top: 1.4rem;
	right: .8rem;
	color: #fff;
	font-size: 1.4rem;
	border-radius: 20px;
	padding: .5rem 1.1rem .3rem;
	background-color: rgba(100,100,100,.5);
	> img {
		width: 1.3rem;
		max-height: 2rem;
		vertical-align: -.1rem;
		margin-right: .6rem;
	}
	&:active {
		color: #fff;
	}
}
.name-initial {
	padding: .7rem 1.2rem;
	font-size: 1.8rem;
	color: #555;
	background-color: #eee;
}
#member-list > ul {
	padding: 0 .8rem;
	background-color: #fff;
}
.member-item {
	position: relative;
	padding: .5rem 0;
	border-bottom: 1px solid #ddd;
	transition: left 150ms;
	&:last-child {
		border-bottom: none;
	}
}
.member-portrait {
	float: left;
	width: 24%;
	height: 8.2rem;
	text-align: center; 
	line-height: 8.2rem;
	> img {
		width: 88%;
		border-radius: 100%;
		max-height: 8rem;
	}
}
.member-item-detail {
	float: left;
	width: 76%;
	height: 8.2rem;
	padding-left: .4rem;
	> h1 {
		font-size: 1.9rem;
		color: #111;
	}
	> p {
		font-size: 1.5rem;
		color: #888;
		margin: .4rem 0;
	}
	> div {
		font-size: 1.5rem;
		color: #333;
		overflow: hidden;
		text-overflow: ellipsis;
		white-space: nowrap;
	}
}
.member-item-delete {
	position: absolute;
	right: -9rem;
	top: 0;
	width: 8rem;
	height: 9.2rem;
	line-height: 9.2rem;
	background-color: $mainColor;
	color: #fff;
	text-align: center;
	font-size: 1.7rem;
}
#member-list {
	overflow: hidden;
}

/*edit-notice*/
.edit-textarea {
	text-align: center;
	textarea {
		width: 99%;
		height: 17rem;
		outline: none;
		color: #444;
		margin-top: 1rem;
		padding: .5rem .8rem;
		border-radius: 5px;
		font-size: 1.6rem;
		resize: none;
		border: 1px solid #ddd;
	}
}