#account-top {
	height: 14.2rem;
	text-align: center;
	background-color: $mainColor;
	> .portrait {
		width: 8rem;
		height: 8rem;
		margin: 1.8rem 0 1rem;
	}
	> p{
		color: #fff;
		font-size: 1.7rem;
	}
}
#account-list {
	font-size: 1.6rem;
	a {
		color: #111;
	}
	p {
		margin: .5rem 0;
		padding: 1rem;
		background-color: #fff;
	}
	> ul {
		background-color: #fff;
		padding: 0 1rem;
	}
	li {
		padding: 1rem 0;
		border-bottom: 1px solid #f0f0f0;
	}
	img {
		float: right;
		width: .8rem;
		margin-top: .5rem;
	}
}
.nav-bottom {
	position: fixed;
	bottom: 0;
	left: 0;
	z-index: 10;
	width: 100%;
	background-color: #fff;
	border-top: 1px solid #eee;
}
.navbt-item {
	float: left;
	width: 33.3%;
	padding: .7rem 0;
	text-align: center;
	color: #999;
	font-size: 1.5rem;
	&.active {
		color: $mainColor;
	}
	> p {
		height: 1.5rem;
	}
	> img {
		max-width: 1.9rem;
		height: 1.9rem;
	}
}